@import "../Css-variables/css-variables.scss";



.wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    width: 100%;
}

.wrapper-home {
    height: 100%;
}

.buttons {
    position: absolute;
    top: 2rem;
    right: 5rem;
}

.description {
    text-align: justify;
    font-size: 13px;
    margin: 0.1rem;
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
    .description {
        font-size: 14px;
    }
}

@media (min-width: 1200px) {
    .description {
        font-size: 17.5px;
    }
}

.container-corner {
    position: absolute;
    right: 10px;
    top: 10px;
}

.btn-circle {
    background: transparent;
    display: block;
    width: 80px;
    height: 80px;
    border: none;
    position: relative;
    z-index: 15;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.line {
    width: 50%;
    height: 3px;
    background: #fff;
    transition:
        transform 0.2s ease,
        opacity 0.2s ease,
        top 0.2s ease 0.2s;
    position: relative;
}

.line:nth-child(1) {
    top: -10px;
}

.line:nth-child(3) {
    top: 10px;
}

.btn-circle.menu-anim .line:nth-child(1) {
    top: 3px;
    transform: rotate(45deg);
    transition:
        top 0.2s ease,
        transform 0.2s ease 0.2s;
}

.btn-circle.menu-anim .line:nth-child(2) {
    opacity: 0;
    transition: opacity 0.2s ease;
}

.btn-circle.menu-anim .line:nth-child(3) {
    top: -3px;
    transform: rotate(-45deg);
    transition:
        top 0.2s ease,
        transform 0.2s ease 0.2s;
}

.circle-menu {
    background: none;
    width: 280px;
    height: 280px;
    border-radius: 50%;
    border: none;
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.4s ease 0.5s;
}

.circle-menu.circle-anim {
    transition: transform 0.4s ease 0.4s;
    transform: translate(-50%, -50%) scale(1);
}

.items-circle {
    border-radius: 50%;
    border: none;
    color: var(--color-white);
    width: 60px;
    height: 60px;
    position: absolute;
    background: var(--color-primary);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    font-size: 25px;
    font-family: 'Poppins',
        sans-serif;
    font-weight: 500;

    &:focus {
        animation: pulse 1s;
        box-shadow: 0 0 0 2em rgba(#fff, 0);
        background: var(--color-additional);
    }
}


@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 var(--color-additional);
    }
}

.items-circle:nth-child(1) {
    left: 0%;
    transition: transform 0.2s ease 0.4s;
}

.items-circle:nth-child(2) {
    left: 15%;
    top: 85%;
    transition: transform 0.2s ease 0.2s;
}

.items-circle:nth-child(3) {
    top: 100%;
    transition: transform 0.2s ease;
}

.circle-menu.circle-anim .items-circle:nth-child(1) {
    transform: translate(-50%, -50%) scale(1);
    transition: transform 0.2s ease 0.7s;
}

.circle-menu.circle-anim .items-circle:nth-child(2) {
    transform: translate(-50%, -50%) scale(1);
    transition: transform 0.2s ease 0.9s;
}

.circle-menu.circle-anim .items-circle:nth-child(3) {
    transform: translate(-50%, -50%) scale(1);
    transition: transform 0.2s ease 1.1s;
}