.testing {
    position: relative;
}

.solar-system {
    width: 16.9rem;
    height: 16.9rem;
    margin: 0;
    position: absolute;
    top: 11px;
    left: 96px;
}

#particles-js {
    height: 33ch;
    width: 50ch;
}

.planet {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid #dedede;
    animation-name: rotatePlanet;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-duration: 15s;
}

.planet:after {
    content: '';
    border-radius: 50%;
    position: absolute;
}

.planet1 {
    width: 100%;
    height: 100%;
    animation-duration: 25s;
}

.planet1::after {
    width: 1.8rem;
    height: 1.8rem;
    top: -15px;
    background: #8a8380;
}

.planet2 {
    width: 75%;
    height: 75%;
    animation-duration: 18s;
}

.planet2:after {
    width: 1.3rem;
    height: 1.3rem;
    top: -10px;
    background: #ab711d;
}

.planet3 {
    width: 77%;
    height: 77%;
    animation-duration: 13s;
}

.planet3:after {
    width: 0.9rem;
    height: 0.9rem;
    top: -8px;
    background: #ffddab;
}

.planet4 {
    width: 55%;
    height: 55%;
    animation-duration: 9s;
}

.planet4:after {
    width: 0.6rem;
    height: 0.6rem;
    top: -6px;
    background: #82c8ff;
}

.sun {
    width: 45%;
    height: 44.5%;
    background: #ffc107;
    border-radius: 50%;
}

@keyframes rotatePlanet {
    from {
        transform: rotate(0);
    }

    to {
        transform: rotate(360deg)
    }
}

@media screen and (min-width: 600px) and (max-width: 1100px) {

    .solar-system {
        width: 15.9rem;
        height: 15.9rem;
        margin: 0;
        top: 21px;
        left: 100px;
    }

    .planet2:after {
        width: 1.2rem;
        height: 1.2rem;
        top: -10px;
        background: #ab711d;
    }

    .planet3:after {
        width: 0.8rem;
        height: 0.8rem;
        top: -6px;
        background: #ffddab;
    }
}